<template>
  <div>
    <v-snackbar v-model="snackbar" :right="true" :timeout="timeout" :top="true" :multi-line="true">
      {{ text }}
      <v-btn color="pink" text @click="snackbar = false">确认</v-btn>
    </v-snackbar>
  </div>
</template>

<script>
export default {
  data() {
    return {
      text: "",
      timeout: 3000,
      snackbar: false
    };
  },
  methods: {
    getProductCheckResult(options) {
      this.text = options.text;
      this.timeout = options.timeout;
      this.snackbar = options.snackbar;
    }
  }
};
</script>